import {Component, OnInit} from '@angular/core';
import {PluginService} from '../service/plugin/plugin.service';
import {catchError} from 'rxjs/operators';
import {of} from 'rxjs';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'URCF Hello World Plugin';

  result = 'No Exec';

  commandList: string[];

  constructor(private pluginService: PluginService) {
  }

  ngOnInit(): void {
    this.pluginService.listPluginCommand('HelloWord')
      .pipe(
        catchError(err => {
          console.log(err);
          return of([]);
        })
      )
      .subscribe(value => {
        this.commandList = value;
      });
  }

  execHello(command: string): void {
    this.pluginService.execPluginCommand('HelloWord', command, [])
      .pipe(
        catchError(err => {
          console.log(err);
          return of({
            result: 'Error Happen'
          });
        })
      )
      .subscribe(
        value => {
          this.result = value.result;
        }
      );
  }
}
