import {Injectable} from '@angular/core';
import {HttpClient, HttpParams} from '@angular/common/http';
import {Observable} from 'rxjs';

export class ExecResult {
  result: string;
}

@Injectable()
export class PluginService {

  constructor(private http: HttpClient) {
  }

  listPluginCommand(pluginName: string): Observable<string[]> {
    return this.http.get<string[]>('http://localhost:8080/v1/plugins/' + pluginName + '/commands');
  }

  execPluginCommand(pluginName: string, command: string, params: string[]): Observable<ExecResult> {
    const httpParams: HttpParams = new HttpParams();
    params.forEach(v => {
      httpParams.append('params', v);
    });
    return this.http.post<ExecResult>('http://localhost:8080/v1/plugins/' + pluginName + '/' + command, {});
  }
}
